.goods_item_wrap {
  width: 45%;
  display: flex;
  flex-direction: column;
  float: left;
  margin-left: 2.5%;
  margin-bottom: 12px;
}
.item_middle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 6px;
  .mid_group {
    display: flex;
    margin-top: 3px;
    .goods_type,
    .goods_extra {
      font-size: 12px;
      color: darkgoldenrod;
    }
    .goods_extra {
      margin-left: 12px;
    }
  }
  .desc_group {
    text-overflow: -o-ellipsis-lastline;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
    .desc {
      color: #666;
      font-size: 14px;
      line-height: 20px;
    }
    .tags {
      padding: 1px 3px;
      font-size: 12px;
      background-color: darkgoldenrod;
      color: #fff;
    }
  }
}
.item_bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2px 6px;
  .stars {
    color: #999;
  }
  .price_wrap {
    color: #ff301a;
  }
  .price_value {
    font-size: 20px;
    font-weight: 700;
  }
}
